import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { useState, useEffect } from 'react';
import { FaQuoteLeft, FaChevronLeft, FaChevronRight } from 'react-icons/fa';

const testimonials = [
  {
    id: 1,
    name: "Jane Smith",
    role: "Product Manager",
    company: "Tech Corp",
    text: "Working with John was a fantastic experience. His attention to detail and technical expertise made our project a huge success.",
    image: "https://via.placeholder.com/60"
  },
  // Add more testimonials...
];

const Testimonials = () => {
  const [current, setCurrent] = useState(0);
  const [direction, setDirection] = useState(1);

  const slideVariants = {
    enter: (direction) => ({
      x: direction > 0 ? 1000 : -1000,
      opacity: 0
    }),
    center: {
      zIndex: 1,
      x: 0,
      opacity: 1
    },
    exit: (direction) => ({
      zIndex: 0,
      x: direction < 0 ? 1000 : -1000,
      opacity: 0
    })
  };

  const swipeConfidenceThreshold = 10000;
  const swipePower = (offset, velocity) => {
    return Math.abs(offset) * velocity;
  };

  const paginate = (newDirection) => {
    setDirection(newDirection);
    setCurrent((prev) => (prev + newDirection + testimonials.length) % testimonials.length);
  };

  useEffect(() => {
    const timer = setInterval(() => paginate(1), 5000);
    return () => clearInterval(timer);
  }, []);

  return (
    <div className="relative w-full max-w-4xl mx-auto px-4 py-12">
      <h2 className="text-3xl font-bold mb-12 text-center">What Clients Say</h2>
      
      <div className="relative h-[300px] overflow-hidden">
        <AnimatePresence initial={false} custom={direction}>
          <motion.div
            key={current}
            custom={direction}
            variants={slideVariants}
            initial="enter"
            animate="center"
            exit="exit"
            transition={{
              x: { type: "spring", stiffness: 300, damping: 30 },
              opacity: { duration: 0.2 }
            }}
            drag="x"
            dragConstraints={{ left: 0, right: 0 }}
            dragElastic={1}
            onDragEnd={(e, { offset, velocity }) => {
              const swipe = swipePower(offset.x, velocity.x);
              if (swipe < -swipeConfidenceThreshold) {
                paginate(1);
              } else if (swipe > swipeConfidenceThreshold) {
                paginate(-1);
              }
            }}
            className="absolute w-full"
          >
            <div className="bg-darker p-8 rounded-xl">
              <FaQuoteLeft className="text-primary text-4xl mb-6" />
              <p className="text-gray-400 text-lg mb-6">{testimonials[current].text}</p>
              <div className="flex items-center">
                <img 
                  src={testimonials[current].image} 
                  alt={testimonials[current].name}
                  className="w-12 h-12 rounded-full mr-4"
                />
                <div>
                  <h4 className="font-bold">{testimonials[current].name}</h4>
                  <p className="text-gray-400 text-sm">
                    {testimonials[current].role} at {testimonials[current].company}
                  </p>
                </div>
              </div>
            </div>
          </motion.div>
        </AnimatePresence>
      </div>

      <div className="flex justify-center gap-4 mt-8">
        <button
          onClick={() => paginate(-1)}
          className="p-2 rounded-full bg-darker text-primary hover:bg-primary hover:text-white transition-colors"
        >
          <FaChevronLeft size={20} />
        </button>
        <button
          onClick={() => paginate(1)}
          className="p-2 rounded-full bg-darker text-primary hover:bg-primary hover:text-white transition-colors"
        >
          <FaChevronRight size={20} />
        </button>
      </div>
    </div>
  );
};

export default Testimonials;
