import React from 'react';
import { motion } from 'framer-motion';
import { FaMapMarkerAlt, FaUniversity, FaCode, FaLanguage } from 'react-icons/fa';
import AnimatedCounter from '../components/AnimatedCounter';
import Testimonials from '../components/Testimonials';
import PageTransition from '../components/PageTransition';

const About = () => {
  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: { staggerChildren: 0.2 }
    }
  };

  const itemVariants = {
    hidden: { y: 20, opacity: 0 },
    visible: {
      y: 0,
      opacity: 1,
      transition: { duration: 0.5 }
    }
  };

  const personalInfo = [
    { icon: <FaMapMarkerAlt />, label: 'Location', value: 'Karachi, Pakistan' },
    { icon: <FaCode />, label: 'Experience', value: '3+ Years' },
    { icon: <FaUniversity />, label: 'Education', value: 'SZABIST Karachi' },
    { icon: <FaLanguage />, label: 'Languages', value: 'English, Urdu' }
  ];

  const skills = [
    { name: 'Web Development', level: 90 },
    { name: 'Python', level: 85 },
    { name: 'Data Structures', level: 80 },
    { name: 'Database Management', level: 85 },
    { name: 'Operating Systems', level: 75 },
    { name: 'Software Engineering', level: 80 }
  ];

  const languages = [
    { name: 'English', level: 95 },
    { name: 'Urdu', level: 100 }
  ];

  const stats = [
    { label: 'Projects Completed', value: 50 },
    { label: 'Happy Clients', value: 30 },
    { label: 'Years Experience', value: 5 },
    { label: 'Awards Won', value: 10 }
  ];

  const courses = [
    "Fundamentals of Programming",
    "Data Structures & Algorithms",
    "Computer Organization & Assembly Language",
    "Database Systems",
    "Python Programming",
    "Artificial Intelligence",
    "Operating Systems",
    "Software Engineering"
  ];

  return (
    <PageTransition>
      <motion.div
        variants={containerVariants}
        initial="hidden"
        animate="visible"
        className="w-full max-w-6xl mx-auto px-4 py-12"
      >
        {/* About Me Section */}
        <motion.div variants={itemVariants} className="mb-16">
          <h2 className="text-4xl font-bold mb-8 text-white relative inline-block group">
            About Me
            <motion.div 
              className="absolute -bottom-2 left-0 w-0 h-0.5 bg-primary group-hover:w-full transition-all duration-300"
              whileHover={{ width: "100%" }}
            />
          </h2>
          <p className="text-gray-400 text-lg leading-relaxed mb-8">
            A passionate Web Developer with 3+ years of experience building modern web applications.
            Currently studying at SZABIST Karachi, I have developed a strong foundation in software
            engineering principles and practical development skills. My academic journey has equipped
            me with comprehensive knowledge in various computer science domains, from low-level
            programming to high-level application development.
          </p>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {personalInfo.map((info, index) => (
              <InfoCard key={index} {...info} />
            ))}
          </div>
        </motion.div>

        {/* Stats Section */}
        <motion.div variants={itemVariants} className="mb-16">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
            {stats.map((stat, index) => (
              <div key={index} className="text-center p-6 bg-darker rounded-xl">
                <h3 className="text-3xl font-bold text-primary mb-2">
                  <AnimatedCounter end={stat.value} />
                </h3>
                <p className="text-gray-400">{stat.label}</p>
              </div>
            ))}
          </div>
        </motion.div>

        {/* Skills Section */}
        <motion.div variants={itemVariants} className="mb-16">
          <h2 className="text-2xl font-bold mb-8 text-white">Technical Skills</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {skills.map((skill, index) => (
              <SkillBar key={index} {...skill} />
            ))}
          </div>
        </motion.div>

        {/* Add Courses Section */}
        <motion.div variants={itemVariants} className="mb-16">
          <h2 className="text-2xl font-bold mb-8 text-white">Completed Courses</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {courses.map((course, index) => (
              <motion.div
                key={index}
                whileHover={{ scale: 1.02 }}
                className="bg-darker p-4 rounded-lg border border-primary/10"
              >
                <span className="text-primary">{course}</span>
              </motion.div>
            ))}
          </div>
        </motion.div>

        {/* Languages Section */}
        <motion.div variants={itemVariants}>
          <h2 className="text-2xl font-bold mb-8 text-white">Languages</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {languages.map((language, index) => (
              <SkillBar key={index} {...language} />
            ))}
          </div>
        </motion.div>

        {/* Testimonials Section */}
        <motion.div variants={itemVariants}>
          <Testimonials />
        </motion.div>
      </motion.div>
    </PageTransition>
  );
};

const InfoCard = ({ icon, label, value }) => (
  <motion.div
    whileHover={{ scale: 1.05 }}
    className="bg-gradient-to-br from-darker to-gray-custom/50 p-6 rounded-xl
      flex items-start gap-4 group hover:shadow-glow transition-all duration-300
      backdrop-blur-sm border border-white/5"
  >
    <span className="text-primary text-xl group-hover:scale-110 transition-transform">
      {icon}
    </span>
    <div>
      <h3 className="text-gray-400 text-sm mb-1">{label}</h3>
      <p className="text-white font-medium">{value}</p>
    </div>
  </motion.div>
);

const SkillBar = ({ name, level }) => (
  <motion.div
    whileHover={{ scale: 1.02 }}
    className="group bg-darker/50 backdrop-blur-sm p-4 rounded-xl border border-white/5"
  >
    <div className="flex justify-between mb-2">
      <span className="text-white">{name}</span>
      <span className="text-primary">{level}%</span>
    </div>
    <div className="h-3 bg-darker rounded-full overflow-hidden">
      <motion.div
        initial={{ width: 0 }}
        animate={{ width: `${level}%` }}
        transition={{ duration: 1, ease: "easeInOut" }}
        className="h-full bg-primary group-hover:shadow-glow transition-shadow"
      />
    </div>
  </motion.div>
);

export default About;
